@import-normalize;

html {
	font-size: 62.5%;
}
body {
	margin: 0;
	font-weight: 300;
	font-size: 11px;
	line-height: 1.4;
	font-family: 'Helvetica Neue', sans-serif;
	color: #444;
	background: #022c43;
	overflow: hidden;
	display: block;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
