@import 'components/Variables.scss';

.contact-form {
	width: 100%;
	margin-top: 20px;

	ul {
		padding: 0;
		margin: 0;

		li {
			padding: 0;
			margin: 0;
			list-style: none;
			margin-bottom: 10px;
			opacity: 0;
			overflow: hidden;
			display: block;
			clear: both;
			position: relative;
			animation: fadeInUp 2s 2s;
			animation-fill-mode: forwards;
		}

		li.half {
			width: 49%;
			margin-left: 2%;
			float: left;
			clear: none;

			&:first-child {
				margin-left: 0;
			}
		}
	}

	input[type='text'],
	input[type='email'] {
		width: 100%;

		background: #115173;
		height: 50px;
		font-size: 16px;
		color: $white-color;
		padding: 0 20px;
		box-sizing: border-box;
		border-radius: 10px;
		transition: $main-transition;
	}
	input[type='text'],
	input[type='email'],
	textarea {
		border: 1px solid #00000000;
		&:focus,
		&:hover {
			outline: none;
			border: 1px solid $primary-color;
		}
	}

	textarea {
		width: 100%;
		background: #115173;
		height: 50px;
		font-size: 16px;
		color: $white-color;
		padding: 20px;
		box-sizing: border-box;
		min-height: 150px;
		border-radius: 10px;
		transition: $main-transition;
		resize: none;
	}

	.flat-button {
		color: $primary-color;
		font-size: 11px;
		letter-spacing: 3px;
		text-decoration: none;
		padding: 8px 10px;
		border: 1px solid $primary-color;
		border-radius: 4px;
		background: 0 0;
		text-transform: uppercase;
		float: right;
		text-align: center;
		margin-right: 10px;
		transition: $main-transition;
		cursor: pointer;
		&:hover {
			background: #ffd700;
			color: #333;
		}
	}
}

.map-wrap {
	background: rgba(8, 253, 216, 0.1);
	float: right;
	width: 53%;
	height: 115%;
	margin-top: -3%;
}

.leaflet-container {
	position: relative;
	width: 100%;
	height: 100%;
	opacity: 0;
	animation: backInRight 1s 1.2s;
	animation-fill-mode: forwards;
}

.info-map {
	position: absolute;
	background: $main-bg-color;
	top: 50px;
	right: 38%;
	z-index: 999999;
	width: fit-content;
	padding: 20px;
	color: #fff;
	font-family: 'Helvetica';
	font-size: 17px;
	font-weight: 300;
	opacity: 0;
	animation: fadeIn 1s 1.5s;
	animation-fill-mode: forwards;

	span {
		font-size: 16px;
		color: $primary-color;
		span {
			color: $primary-color;
		}
	}
	.contacts-mail-link {
		font-family: 'Helvetica';
		text-decoration: none;
		font-weight: 300;
		color: $primary-color;
		transition: $main-transition;
		&:hover {
			text-decoration: underline;
		}
	}
}

@media screen and (max-width: 1200px) {
	.map-wrap {
		float: none;
		margin: 0;
		width: 100%;
		height: 400px;
	}

	.info-map {
		display: none;
	}
}
